.comparison-table {
    margin: 20px;
    padding: 20px;
    border-collapse: collapse;
  }
  
  .comparison-table th, .comparison-table td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .comparison-table th {
    background-color: #f2f2f2;
  }
  
  .comparison-table td img {
    width: 50px;
    height: auto;
  }
  