.sidebar {
    /* 既存のスタイル */
}

/* 検索フォームのスタイル */
.search-form {
    margin-top: 20px;
}

.select-wrap {
    margin-bottom: 10px;
}

.select select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.input {
    margin-top: 10px;
}

.check_btn {
    display: inline-block;
    margin-right: 10px;
    background-color: #f5f5f5;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
}

.button {
    margin-top: 20px;
    text-align: center;
}

.button a {
    display: inline-block;
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border-radius: 4px;
    text-decoration: none;
}
